@charset "UTF-8";

/* gx */
#gxHtml {
    height:100%; max-width:inherit; overflow-x:hidden;
    body {height:100%;}
    #wrap {
        width:max-content; height:100%; position:fixed !important; padding:0;
        @include boxSizing;
        @include transform(translateX(0));
    }
    #header {
        &.gx_header {
            background-color:$white !important; top:50px; position:fixed;
            &:after {background-color:$gray3 !important;}
            .header_wrap {
                #logo a {background-image:url(../../images/layout/logo_fix.png) !important;}
                #allNav {
                    button {
                        &:before,
                        &:after,
                        span {background-color:$gray1 !important;}
                    }
                }
                .header_buy {
                    color:$gray1 !important;
                    &:before {background-image:url(../../images/layout/buy_icon_fix.png) !important;}
                    &:after {background-color:$gray1 !important;}
                }
            }
            &.fixed {top:0}
        }
    }
    #footer {width:100%; position:fixed; bottom:em(72); background-color:$white; z-index:100;}
    #leftToRight {
        height:100%; will-change:content; position:relative;
        @extend %clearfix;
        > section {
            height:100%; float:left; position:relative;
            @include boxSizing;
        }
        //1번 섹션
        .gx_contents01 {
            margin:0; z-index:10; padding:70px 0 em(72);
            img {height:100%;}
            .txt_wrap {
                width:100%; line-height:em(88); position:absolute; bottom:em(100); text-align:center; top:68%;
                h3 {font-size:em(88);}
                p {font-size:em(80);}
            }
        }
        //2번 섹션
        .gx_contents02 {
            padding:70px 0 em(72) 120px;
            @extend %clearfix;
            .txt_wrap {
                height:100%; float:left; position:relative;
                @include displayFlexCenter;
                .tit {
                    h3 {font-size:76px;}
                    p {font-size:22px; line-height:30px; padding:em(30) 0 0; color:$gray2;}
                }
            }
            .brand_wrap {
                width:815px; position:absolute; bottom:0; border-top:1px solid $gray3; padding:em(45) 0 0;
                p {font-size:26px; line-height:36px; padding:10px 0 0; font-weight:$regular;}
                a {
                    display:inline-block; padding:10px 25px; margin:15px 0 0; font-weight:$bold;
                    @include borderSet(2px, solid, $gray3);
                    @include borderRadius(4px);
                    @include transition;
                    &:hover,
                    &:focus {border-color:$gray1; color:$white; background-color:$gray1;}
                }
            }
            .step_img {
                height:96%; float:left; margin:0 0 0 em(220); z-index:10; position:relative; top:4%;
                img {height:100%;}
            }
        }
        //3번 섹션
        .gx_contents03 {
            padding:70px 0 em(72) 220px;
            @extend %clearfix;
            .txt_wrap {
                height:100%; float:left; position:relative;
                @include displayFlexCenter;
                .tit {
                    h3 {font-size:76px;}
                    p {font-size:22px; line-height:30px; padding:em(30) 0 0; color:$gray2;}
                    a {
                        display:inline-block; padding:10px 25px; margin:15px 0 0; font-weight:$bold;
                        @include borderSet(2px, solid, $gray3);
                        @include borderRadius(4px);
                        @include transition;
                        &:hover,
                        &:focus {border-color:$gray1; color:$white; background-color:$gray1;}
                    }
                }
            }
            .brand_wrap {
                position:absolute; bottom:0; left:0;
                p {font-size:26px; line-height:36px; padding:10px 0 0; font-weight:$regular;}
            }
            .step_img {
                height:96%; float:left; z-index:10; overflow:hidden; position:relative; top:4%; margin:0 0 0 em(220);
                img {height:100%;}
                .screen_img {position:absolute; left:50px; top:0;}
                p {
                    position:absolute; left:30px; bottom:10px; color:$white; font-size:16px; line-height:24px; font-weight:$regular;
                    strong {font-size:20px; font-weight:$regular;}
                }
            }
        }
        //4번 섹션
        .gx_contents04 {
            background-color:#f6f6f6; padding:70px 100px em(72) 120px; margin:0 0 0 120px;
            @include displayFlexCenter;
            @extend %clearfix;
            .txt_wrap {
                height:100%; float:left; position:relative;
                @include displayFlexCenter;
                .tit {
                    padding:0 0 25%;
                    h3 {font-size:76px;}
                    p {font-size:22px; line-height:30px; padding:em(30) 0 0; color:$gray2;}
                }
            }
            .brand_wrap {
                height:30%; position:absolute; bottom:3%;
                p {font-size:26px; line-height:36px; padding:10px 0 0; font-weight:$regular;}
                img {height:100%;}
            }
            .step_img {
                height:86%; float:left; margin:0 0 0 em(220); z-index:10; position:relative; padding:0 59.5% 0  0; top:3.5%;
                @include boxSizing;
                .video_wrap {width:100%; height:100%; position:absolute; left:0; top:0;}
                button {overflow:hidden;}
            }
        }

        //5번 섹션
        .gx_contents05 {
            padding:70px 0 em(72) 120px;
            @extend %clearfix;
            .txt_wrap {
                height:100%; float:left; position:relative;
                @include displayFlexCenter;
                .tit {
                    h3 {font-size:76px;}
                    p {font-size:22px; line-height:30px; padding:em(30) 0 0; color:$gray2;}
                    .link_wrap {margin:80px 0 0;}
                    a {
                        display:inline-block; padding:10px 25px; font-weight:$bold;
                        @include borderSet(2px, solid, $gray3);
                        @include borderRadius(4px);
                        @include transition;
                        &:hover,
                        &:focus {border-color:$gray1; color:$white; background-color:$gray1;}
                    }
                }
            }
            .step_img {
                height:96%; float:left; margin:0 0 0 em(220); z-index:10; overflow:hidden; position:relative; top:4%;
                img {height:100%;}
                .screen_img {position:absolute; left:50px; top:0;}
            }
        }

        //6번 섹션
        .gx_contents06 {
            padding:0 0 0 120px; overflow:hidden;
            @extend %clearfix;
            @include displayFlexCenter;
            .txt_wrap {
                height:100%; float:left; position:relative;
                @include displayFlexCenter;
                .tit {
                    h3 {
                        font-size:76px;
                        span {font-weight:$light;}
                    }
                    p {font-size:22px; line-height:30px; padding:em(30) 0 0; color:$gray2;}
                    .link_wrap {margin:80px 0 0;}
                }
            }
            .step_img {
                width:57vw; height:100%; float:left; margin:0 0 0 em(220); z-index:10; position:relative;
                img {height:210%; position:absolute; right:0;}
            }
        }

        //7번 섹션
        .gx_contents07 {
            margin:0 0 0 220px;
            @extend %clearfix;
            .txt_wrap {
                height:100%; float:left; position:relative;
                @include displayFlexCenter;
                .tit {
                    h3 {font-size:76px;}
                    p {font-size:22px; line-height:30px; padding:em(30) 0 0; color:$gray2;}
                    a {
                        display:inline-block; padding:10px 25px; margin:75px 0 0; font-weight:$bold;
                        @include borderSet(2px, solid, $gray3);
                        @include borderRadius(4px);
                        @include transition;
                        &:hover,
                        &:focus {border-color:$gray1; color:$white; background-color:$gray1;}
                    }
                    .js-pall-con01 {left:-2%;}
                }
            }
            .disclaimer {position:absolute; bottom:em(72); font-size:16px; line-height:24px; font-weight:$regular; left:-6%;}
            .step_img {
                height:100%; float:left; margin:0 0 0 em(220); z-index:10; overflow:hidden; position:relative;
                img {height:100%;}
                .screen_img {position:absolute; left:-50px; top:0;}
            }
        }

        //8번 섹션
        .gx_contents08 {
            padding:70px 0 em(72) 120px;
            @include displayFlexCenter;
            @extend %clearfix;
            .txt_wrap {
                height:100%; float:left; position:relative;
                @include displayFlexCenter;
                .tit {
                    h3 {
                        font-size:76px; letter-spacing:0.3px;
                        span {font-weight:$light;}
                    }
                    p {font-size:22px; line-height:30px; padding:em(30) 0 0; color:$gray2;}
                }
            }
            .step_img {
                height:86%; float:left; margin:0 0 0 em(220); z-index:10; position:relative; padding:0 59.5% 0  0; top:3.5%;
                @include boxSizing;
                .video_wrap {width:100%; height:100%; position:absolute; left:0; top:0;}
                button {overflow:hidden;}
            }
        }

        //9번 섹션
        .gx_contents09 {
            .section_box {width:100%;text-align:center; position:absolute; top:50%; margin:-308px 0 0;}
            h3 {font-size:64px;}
            ol {
                margin:80px 0 0;
                @extend %clearfix;
                li {
                    width:286px; height:430px; float:left; background-color:#f4f5f6; padding:40px 40px 50px 40px; position:absolute; left:50%;
                    &:nth-of-type(1) {margin:0 0 0 -575px;}
                    &:nth-of-type(2) {margin:0 0 0 -200px;}
                    &:nth-of-type(3) {margin:0 0 0 175px;}
                }
                .tit {padding:35px 0 0;}
                .brand_txt {font-size:32px; font-weight:$bold;}
                .inch_txt {width:216px; margin:18px 0 0; display:inline-block; color:#6e6e6e; font-size:26px; font-weight:$bold; padding:18px 0 0; border-top:1px solid #c4c6c8;}
                .link_wrap {
                    margin:32px 0 0;
                    a {
                        width:210px; display:inline-block; padding:10px 0; margin:5px 0 0; font-weight:$semibold; font-size:22px;
                        @include borderSet(2px, solid, $gray3);
                        @include borderRadius(4px);
                        @include transition;
                        &:hover,
                        &:focus {border-color:$gray1; color:$white; background-color:$gray1;}
                    }
                }
            }
        }
    }
    .js-pall-con01, .js-pall-con02, .js-pall-con03 {position:relative; left:0;}
}


































